<template>
    <div class="main">
        <Back imgUrl="/img/back_2.png" class="backImg"></Back>
        <div class="collect_title">
            <slot name="title"></slot>
        </div>
        <div class="content">
            <slot name="content"></slot>
        </div>
    </div>
</template>
<script>
import Back from '../product/back.vue'
export default {
    components: { Back },
}
</script>
<style lang="scss">
* {
    margin: 0;
    padding: 0
}

.main {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
}

.collect_title {
	position: fixed;
	top: 0;
	background-color: #fff;
	box-sizing: border-box;
    width: 100%;
    max-width: 640px;
    height: 50px;
    margin: 0 auto;
    padding-left: 60px;
    color: #939;
    display: flex;
    align-items: center;
    z-index: 2;
}

.content {
    width: 100%;
    margin-top: 50px;
}
</style>


