<template>
<div class="back" @click="back">
	<img :src="backPng" alt="">
</div>	
</template>

<script>
export default{
	props: ["imgUrl"],
	data(){
		return{
			backPng: this.imgUrl || "/img/product_back.png"
		}
	},
	methods: {
		back: function(){
			window.history.go(-1)
		}
	}
}
</script>

<style lang="scss" scoped>
.back{
	width: 45px;
	height: 45px;
	position: fixed;
	display: flex;
	align-items: center;
	z-index: 10;
	top: 3px;
	margin-left: 5px;
	img{
		width: 100%;
	}
}	

</style>


