<template>
    <div class="qna_container">
        <div class="qna_item" v-for="(item, index) in list">
            <div class="question" @click="change(index)">{{item.question}}</div>
            <div class="answer" :class="[visible[index] ? 'visible' : 'hidden']">{{item.answer}}</div>
        </div>
    </div>
</template>
<script>
export default {
    props: ["list"],
    data() {
        return {
            visible: [],
            visibleClass: 'visible',
            openIndex: 0
        }
    },
    created: function() {
        this.visible = this.list.map(() => false)
        this.visible[0] = true;
    },
    methods: {
        change: function(index) {
            var value = !this.visible[index];
			this.visible.splice(this.openIndex, 1, false);
			this.openIndex = index;
            this.visible.splice(index, 1, value);
        }
    }
}
</script>
<style lang="scss" scoped>
.qna_item {
    width: 100%;
    padding: 15px 0;
}

.question {
    width: 100%;
    height: 35px;
    box-sizing: border-box;
    padding-left: 15px;
    position: relative;
    display: flex;
    align-items: center;
    background-color: #fff;
    color: #666;
    &:before {
        content: "";
        display: block;
        height: 30px;
        width: 2px;
        background-color: #939;
        position: absolute;
        left: 5px;
        top: 2px;
    }
}

.answer {
    width: 100%;
    box-sizing: border-box;
    background-color: #fafafa;
    color: #888;
    transform: tranlate3d(0,0,0);
    transition: all 0.3s;
    overflow: hidden;
    padding: 0 20px;
}

.visible {
    height: auto;
    padding-top: 15px;
    padding-bottom: 15px;
}

.hidden {
    height: 0;
}
</style>


