<template>
    <BackTitle>
        <span slot="title">常见问题</span>
        <Qna :list="question" slot="content" class="question_container"></Qna>
    </BackTitle>
</template>
<script>
import Qna from './qna.vue'
import BackTitle from './back_with_title.vue'
export default {
    components: { Qna, BackTitle },
    data() {
        return {
            question: []
        }
    },
    created: function() {
        for(var i=0; i<5; i++){
        	this.question.push({ question: "这是什么？", answer: "这是答案" })
        }
    }
}
</script>
<style lang="scss">
.main {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
}
.question_container{
	padding-top: 5%;
	width: 90%;
	margin: 0 auto;
}
</style>


